ul {
  list-style-type: none; }

.grid-container {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto; }

.grid-container > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 20px; }

.item {
  border: 1px solid black; }

:root {
  --square-size: 15px;
  --square-gap: 5px;
  --week-width: calc(var(--square-size) + var(--square-gap)); }

.months {
  grid-area: months; }

.days {
  grid-area: days; }

.squares {
  grid-area: squares; }

.graph {
  display: inline-grid;
  grid-template-areas: "empty months" "days squares";
  grid-template-columns: auto 1fr;
  grid-gap: 10px; }

.months {
  display: grid;
  grid-template-columns: calc(var(--week-width) * 4) calc(var(--week-width) * 4) calc(var(--week-width) * 4) calc(var(--week-width) * 5) calc(var(--week-width) * 4) calc(var(--week-width) * 4) calc(var(--week-width) * 5) calc(var(--week-width) * 4) calc(var(--week-width) * 4) calc(var(--week-width) * 5) calc(var(--week-width) * 4) calc(var(--week-width) * 5); }

.days,
.squares {
  display: grid;
  grid-gap: var(--square-gap);
  grid-template-rows: repeat(7, var(--square-size)); }

.squares {
  grid-auto-flow: column;
  grid-auto-columns: var(--square-size); }

/* Other styling */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 12px; }

.graph {
  padding: 20px;
  border: 1px #e1e4e8 solid;
  margin: 20px; }

.days li:nth-child(odd) {
  visibility: hidden; }

.squares li {
  background-color: #ebedf0; }

.squares li[data-level="1"] {
  background-color: #c6e48b; }

.squares li[data-level="2"] {
  background-color: #7bc96f; }

.squares li[data-level="3"] {
  background-color: #196127; }
